import React from 'react';
import Home from './Home';
import checkMobile from '../Services/checkIfMobile';

const IfMobileContext = React.createContext(false);

const EntryPoint = props => {
  const { ifMobile } = props;
  return (
    <IfMobileContext.Provider>{ifMobile => <Home ifMobile={ifMobile} />}</IfMobileContext.Provider>
  );
};

EntryPoint.getInitialProps = async ({ req }) => {
  const userAgent = req.headers['user-agent'];
  const ifMobile = checkMobile(userAgent);
  return { ifMobile };
};

export default EntryPoint;
